import React from "react";
import Image from "next/image";

import Link from "next/link";
import { Button } from "../ui/button";
import { Tabs, TabsList, TabsTrigger } from "../ui/tabs";

const MENULINKS = [
  { name: "Home", href: "/" },
  { name: "Courses", href: "/courses" },
  { name: "About", href: "/about" },
  { name: "Contact", href: "/contact" },
];

function NavBar() {
  return (
    <nav className=" h-[99px] flex items-center justify-between px-[132px] pt-5 pb-6">
      <div className=" h-[55px] flex items-center gap-[50px]">
        <div className=" w-[54px] h-[54px] rounded-lg bg-primary flex items-center justify-center">
          <Link href={"/"}>
            <Image
              src={"/assets/shape-17.png"}
              alt="logo"
              width={32.5}
              height={32.5}
              className="object-fill"
            />
          </Link>
        </div>
        <div className="flex gap-[26px]">
          {MENULINKS.map((link) => (
            <Button
              key={link.href}
              className=" text-lg w-[100px] h-[55px] rounded-lg px-6 py-[14px]"
            >
              <Link href={link.href}> {link.name}</Link>
            </Button>
          ))}
        </div>
      </div>
      <Tabs
        defaultValue="login"
        className=" flex items-center justify-center h-[55px] w-[214px]"
      >
        <TabsList className=" h-full text-lg">
          <TabsTrigger value="sign up" className=" text-lg h-full">
            {" "}
            <Link href="/signup">Sign Up</Link>{" "}
          </TabsTrigger>
          <TabsTrigger value="login" className=" text-lg h-full">
            <Link href="/login">Login</Link>{" "}
          </TabsTrigger>
        </TabsList>
      </Tabs>
    </nav>
  );
}

export default NavBar;
